@mixin background-image {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  padding: 100px 0 50px;
  border-bottom: 1px solid $drk-blue;
}

@mixin background-image-before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: url(/assets/bg_pattern.png);
  background-repeat: repeat;
  background-color: rgba(0, 58, 87, 0.5);
}

@mixin header-over-background {
  position: relative;
  color: #fff;
  text-shadow: 0 1px 1px $drk-blue;
  margin: 0 0 0.5em;
  font-size: 2.25em;
  line-height: 1.25;
}

/**
 * Site header
 */
.site-header {
  border-bottom: 1px solid $border-color-01;
  min-height: $spacing-unit * 1.865;
  line-height: $base-line-height * $base-font-size * 2.25;
  text-align: center;
  background-color: #fff;
  position: relative;
  z-index: 100;

  // Positioning context for the mobile navigation icon
  position: relative;
  z-index: 10;
}

.site-title {
  font-family: $base-font-family;
  font-weight: 300;
  text-transform: uppercase;
  font-style: italic;
  padding: 0;
  font-size: 1.75em;
  line-height: 1em;
  margin: 7px auto;
  text-align: center;
  letter-spacing: -.05em;

  a {
    color: #777;

    svg {
      width: 24px;
      height: auto;
      fill: $blue;
    }

    &:hover {
      color: $link-hover-color;
      text-decoration: none;
    }
  }
}

.site-nav {
  position: absolute;
  top: 9px;
  right: $spacing-unit / 2;
  background-color: $background-color;
  border: 1px solid $border-color-01;
  border-radius: 5px;
  text-align: right;

  .nav-trigger {
    display: none;
  }

  .menu-icon {
    float: right;
    width: 36px;
    height: 26px;
    line-height: 0;
    padding-top: 10px;
    text-align: center;

    >svg path {
      fill: $border-color-03;
    }
  }

  label[for="nav-trigger"] {
    display: block;
    float: right;
    width: 36px;
    height: 36px;
    z-index: 2;
    cursor: pointer;
  }

  input~.trigger {
    clear: both;
    display: none;
  }

  input:checked~.trigger {
    display: block;
    padding-bottom: 5px;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;
    display: block;
    padding: 5px 10px;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 0;
    }

    margin-left: 20px;
  }

  @media screen and (min-width: $on-medium) {
    position: static;
    float: right;
    border: none;
    background-color: inherit;

    label[for="nav-trigger"] {
      display: none;
    }

    .menu-icon {
      display: none;
    }

    input~.trigger {
      display: block;
    }

    .page-link {
      display: inline;
      padding: 0;

      &:not(:last-child) {
        margin-right: 20px;
      }

      margin-left: auto;
    }
  }
}

.post {
  position: relative;
  z-index: 1;

  img {
    width: 100%;
    height: auto;
  }
}

.post-wrapper {
  position: relative;
  z-index: 5;
  background-color: #fff;
  padding-top: 2em;
  padding-bottom: 1em;
}

/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $border-color-01;
  padding: $spacing-unit 0;
  text-align: center;
  position: relative;
  z-index: 100;
  background: #fff;

  ul {
    list-style: none;

    li {
      display: inline-block;
      margin: 0 5px;

      a {
        color: $blue;
        font-weight: bold;
      }
    }
  }
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper,
.social-links {
  @include relative-font-size(0.9375);
  color: $brand-color;
}

.footer-col {
  margin-bottom: $spacing-unit / 2;
}

.footer-col-1,
.footer-col-2 {
  width: calc(50% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: calc(100% - (#{$spacing-unit} / 2));
}

@media screen and (min-width: $on-large) {
  .footer-col-1 {
    width: calc(35% - (#{$spacing-unit} / 2));
  }

  .footer-col-2 {
    width: calc(20% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: calc(45% - (#{$spacing-unit} / 2));
  }
}

@media screen and (min-width: $on-medium) {
  .footer-col-wrapper {
    display: flex
  }

  .footer-col {
    width: calc(100% - (#{$spacing-unit} / 2));
    padding: 0 ($spacing-unit / 2);

    &:first-child {
      padding-right: $spacing-unit / 2;
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
      padding-left: $spacing-unit / 2;
    }
  }
}



/**
 * Page content
 */
.page-content {
  padding: 0;
  flex: 1 0 auto;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  margin-bottom: 0;
  background: $drk-blue;
  list-style: none;
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: 400px;
  grid-auto-flow: dense;


  li {
    position: relative;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    border: 1px solid $drk-blue;
    display: flex;
    align-items: center;
    justify-content: left;
    transform: rotate(1deg);

    &:nth-child(even) {
      transform: rotate(-1deg);
      background: $blue;
    }

    &.post-teaser-has-image {
      @include background-image();
      margin: 0;
      padding: 0;
    }

    &:before {
      @include background-image-before();
    }

    .post-content {
      padding: 50px 25px 10px;
    }

    .post-content {
      @include header-over-background();


      h3 {
        position: relative;
        font-size: 1.5em;
        margin-top: 10px;
        margin-bottom: 20px;

        a {
          color: #fff;

          &:hover {
            color: #eee;
          }
        }
      }

      .post-meta {
        position: relative;
        font-size: 18px;
        color: #eee;
        display: block;
      }
    }
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $brand-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: 0;


  &.post-header-is-image {
    @include background-image();

    &:before {
      @include background-image-before();
    }

    h1 {
      @include header-over-background();
    }
  }

  .post-meta {
    position: relative;
    color: #eee;
    text-shadow: 0 1px 1px $drk-blue;
  }
}

.post-title,
.post-content h1 {
  @include relative-font-size(2.625);
  letter-spacing: -1px;
  line-height: 1.15;

  @media screen and (min-width: $on-large) {
    @include relative-font-size(2.625);
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h1,
  h2,
  h3 {
    margin-top: $spacing-unit * 2
  }

  h4,
  h5,
  h6 {
    margin-top: $spacing-unit
  }

  h2 {
    @include relative-font-size(1.75);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(2);
    }
  }

  h3 {
    @include relative-font-size(1.375);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.625);
    }
  }

  h4 {
    @include relative-font-size(1.25);
  }

  h5 {
    @include relative-font-size(1.125);
  }

  h6 {
    @include relative-font-size(1.0625);
  }
}


.social-media-list {
  display: table;
  margin: 0 auto;

  li {
    float: left;
    margin: 5px 10px 5px 0;

    &:last-of-type {
      margin-right: 0
    }

    a {
      display: block;
      padding: $spacing-unit / 4;
      border: 1px solid $border-color-01;

      &:hover {
        border-color: $border-color-02
      }
    }
  }
}



/**
 * Pagination navbar
 */
.pagination {
  margin-bottom: $spacing-unit;
  @extend .social-media-list;

  li {

    a,
    div {
      min-width: 41px;
      text-align: center;
      box-sizing: border-box;
    }

    div {
      display: block;
      padding: $spacing-unit / 4;
      border: 1px solid transparent;

      &.pager-edge {
        color: $border-color-01;
        border: 1px dashed;
      }
    }
  }
}



/**
 * Grid helpers
 */
@media screen and (min-width: $on-large) {
  .one-half {
    width: calc(50% - (#{$spacing-unit} / 2));
  }
}
